button {
    width: 200px;
    height: 40px;
    border-radius: 15px;
    background-color: pink;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
    cursor: pointer;
    border: none;
    margin: 0 auto;
}

button:hover {
    background-color: rgba(255, 192, 203, 0.7);
}